body {
  box-sizing: border-box;
  --blue-color: #00acdc;
  --white-color: #fff;
  --black-color: #000;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header {
  box-sizing: border-box;
  background-color: var(--white-color);
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  padding: 0 50px;
  display: flex;
}

.nav__link {
  color: var(--black-color);
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  height: 60px;
  margin: 0 10px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: var(--blue-color);
}

.header__link {
  align-items: center;
  display: flex;
}

.nav__link.is-active {
  color: var(--blue-color);
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:first-of-type .nav__link {
  margin-left: 0;
}

.nav__item:last-of-type .nav__link {
  margin-right: 0;
}

